import { Pipe, PipeTransform } from '@angular/core';
/**
 * Filters the files schema as per the datatype for index value in timeseries project only
 */
@Pipe({
  name: 'indexDataType',
})
export class IndexDataTypePipe implements PipeTransform {
  transform(items: Array<any>): Array<any> {
    items = items.filter((item) => item.dataType === 'Datetime');
    return items;
  }
}
