import { LoginService } from './../pages/login/login.service';
import { UtilsService } from './../../shared/utils.service';
import { UsersService } from './../dashboard/users/users.service';
import { LocalStorageService } from './../../shared/local-storage.service';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators, AbstractControl } from '@angular/forms';
import { CustomValidators } from 'ngx-custom-validators';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { ToastrService } from 'ngx-toastr';
import { PsCustomValidators } from 'src/app/shared/ps-custom-validators';
import { Router } from '@angular/router';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss'],
})
export class ProfileComponent implements OnInit, OnDestroy {
  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  newPassword = new FormControl('', [
    Validators.required,
    Validators.maxLength(32),
    Validators.minLength(8),
    Validators.pattern('^[^\\s]+'),
    PsCustomValidators.passwordPolicies,
  ]);

  hideCurrPassword: boolean = true;
  hidePassword: boolean = true;
  hideNewPassword: boolean = true;
  hideConfirmPassword: boolean = true;

  personalDetailsForm: FormGroup;
  passwordForm: FormGroup;
  settingActive: number = 1;
  user: any;

  constructor(
    private _fb: FormBuilder,
    private _localStorage: LocalStorageService,
    private _users: UsersService,
    private _toaster: ToastrService,
    private _utils: UtilsService,
    private _router: Router,
    private _login: LoginService
  ) {
    // Password form structure
    this.passwordForm = this._fb.group({
      currentPassword: [null, Validators.required],
      verifyPassword: [null, [Validators.required, CustomValidators.equalTo(this.newPassword)]],
    });

    // Personal details form structure
    this.personalDetailsForm = this._fb.group({
      firstName: [null, Validators.required],
      lastName: [null, Validators.required],
      username: [null, Validators.required],
      email: [null, Validators.compose([Validators.required, CustomValidators.email])],
    });
  }

  ngOnInit(): void {
    this.user = this._localStorage.getItem('user');
    this.personalDetailsForm.patchValue({
      firstName: this.user.firstName,
      lastName: this.user.lastName,
      username: this.user.username,
      email: this.user.email,
    });
  }

  /**
   * Updates the users personal details
   * @param formDetails
   */
  updatePersonalDetails(formDetails: any): void {
    this.user.firstName = formDetails.firstName;
    this.user.lastName = formDetails.lastName;
    this.user.username = formDetails.username;
    this.user.email = formDetails.email;
    this._users
      .updateUser(this.user)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this._localStorage.setItem('user', this.user);
            this._utils.userProfileSubject(this.user);
            this.personalDetailsForm.patchValue({
              firstName: this.user.firstName,
              lastName: this.user.lastName,
              username: this.user.username,
              email: this.user.email,
            });
            this._toaster.success('Personal details updated successfully', 'Profile');
          } else {
            this._toaster.error('Cannot update personal details', 'Profile');
          }
        },
        (err: any) => {
          this._toaster.error(err.error?.message || 'Server Error', 'Profile');
        }
      );
  }

  /**
   * Updates the user password
   * @param formDetails
   */
  updatePassword(formDetails: any): void {
    formDetails.newPassword = this.newPassword.value;
    this._users
      .updateUserPassword(formDetails)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this.newPassword.clearValidators();
            this.newPassword.updateValueAndValidity();
            this.passwordForm.get('currentPassword').clearValidators();
            this.passwordForm.get('currentPassword').updateValueAndValidity();
            this.passwordForm.get('verifyPassword').clearValidators();
            this.passwordForm.get('verifyPassword').updateValueAndValidity();
            this.passwordForm.reset();
            this.newPassword.reset();
            this._toaster.success('Password updated successfully', 'Profile');
            this.logoutUser();
          } else {
            this._toaster.error('Cannot update the password', 'Profile');
          }
        },
        (err: any) => {
          this._toaster.error(err.error.message || 'Server Error', 'Profile');
        }
      );
  }

  /**
   * Signouts the user
   */
  logoutUser(): void {
    this._login.logoutUser().subscribe(
      (res) => {},
      (err: HttpErrorResponse) => {
        this._localStorage.deleteItem('project');
        this._localStorage.deleteItem('user');
        this._localStorage.deleteItem('paymentCaptured');
        window.location.reload();
      }
    );
  }

  /**
   * Perform operations when the module is destroyed
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
