import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root',
})
export class DashboardService {
  serverBaseUrl: string = environment.serverBaseUrl;
  formData: FormData = new FormData();

  constructor(private _http: HttpClient) { }

  /**
   * Fetches all the projects list of logged user
   */
  getAllProjectsList(): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/projects`);
  }

  /**
   * Fetches all the projects list of logged user
   */
  createNewProject(projectDetails: any): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/projects`, projectDetails);
  }

  /**
   * Updates the project details
   * @param projectDetails
   */
  updateProjectDetails(projectDetails: any, projectId: string): Observable<any> {
    return this._http.put(`${this.serverBaseUrl}/api/projects/${projectId}`, projectDetails);
  }

  /**
   * Uploads the existing project
   * @param file project file details
   */
  uploadProject(file: any, uplaodType?: string): Observable<any> {
    this.formData.set('file', file, file.name);
    return this._http.post(
      `${this.serverBaseUrl}/api/project/import?uploadType=importProject`,
      this.formData
    );
  }

  /**
   * Imports the file if selected from sample templates
   * @param filename
   * @returns
   */
  importTemplateProject(filename: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/project/import?uploadType=importTemplate`,
      filename
    );
  }

  /**
   * Deletes the project
   * @param projectId
   */
  deleteProject(projectId: string, deleteType: string): Observable<any> {
    return this._http.delete(`${this.serverBaseUrl}/api/projects/${projectId}?deleteType=${deleteType}`);
  }

  /**
   * Fetches the license information
   */
  getLicenseInfo(): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/project/licenseInfo`);
  }

  /**
   * Generates a download link for the project for export
   */
  generateDownloadLink(projectId: string): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/projects/${projectId}/export`);
  }

  /**
   * Gets the generated download link
   * @param projectId selected project id
   */
  getDownloadLink(projectId: string): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/projects/${projectId}/download`);
  }

  /**
   * Fetches the particular project comments
   * @param projectId selected project
   */
  fetchProjectComments(projectId: string): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/projects/${projectId}/discuss`);
  }

  /**
   * Adds comments to the particular project
   * @param message comment
   * @param projectId selected project
   */
  addProjectComments(message: object, projectId: string): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/projects/${projectId}/discuss`, message);
  }

  /**
   * Shares the project to the provided email
   * @param email receivers id
   * @param projectId selected project id
   */
  shareProject(email: object, projectId: string): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/projects/${projectId}/share`, email);
  }

  /**
   * function to get the list of uploaded models by the admin.
   * @param taskType this is the identifier for the origin of the request.
   * @returns
   */
  taModel(taskType: string): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/v2/upload/model`, {
      params: { taskType: taskType },
    });
  }

  /**
   * Fetches the machine details from server
   * @returns
   */
  getMachineDetails(): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/settings/pscoreIdCreation`);
  }

  getProjectCount(): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/v2/project/projectCount`);
  }
}
