import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root',
})
export class CreateConnectionService {
  serverBaseUrl = environment.serverBaseUrl;
  constructor(private _htttp: HttpClient) {}

  /**
   * Test the connection with DB for success or not
   * @param connectionDetials
   */
  testDBConnection(connectionDetials: any): Observable<any> {
    return this._htttp.post(
      `${this.serverBaseUrl}/api/v2/dbconn/test/connection`,
      connectionDetials
    );
  }

  /**
   * Test the connection is success or not
   * @param connectionDetials
   */
  testConnection(connectionDetials: any, source: string): Observable<any> {
    return this._htttp.post(
      `${this.serverBaseUrl}/api/connections/${source}/test`,
      connectionDetials
    );
  }

  /**
   * Test the connection with SFTP for success or not
   * @param connectionDetials
   */
  testSFTPConnection(connectionDetials: any): Observable<any> {
    return this._htttp.post(`${this.serverBaseUrl}/api/v2/sftp/test/connection`, connectionDetials);
  }

  /**
   * Fetches all DB connections list
   */
  getDbConnectionList(): Observable<any> {
    return this._htttp.get(`${this.serverBaseUrl}/api/v2/dbconn`);
  }

  /**
   * Fetches all DB connections list
   */
  getSftpConnectionList(): Observable<any> {
    return this._htttp.get(`${this.serverBaseUrl}/api/v2/sftp`);
  }

  /**
   * Fetches all connections list as per the type
   */
  getConnectionsList(type: string): Observable<any> {
    return this._htttp.get(`${this.serverBaseUrl}/api/connections/${type}`);
  }

  /**
   * Creates the new connection as per the type
   */
  createNewConnection(type: string, reqObj: any): Observable<any> {
    return this._htttp.post(`${this.serverBaseUrl}/api/connections/${type}`, reqObj);
  }

  /**
   * Creates a new database connection
   * @param connectionDetials
   */
  createDBConnection(connectionDetials: any): Observable<any> {
    return this._htttp.post(`${this.serverBaseUrl}/api/v2/dbconn`, connectionDetials);
  }

  /**
   * Updates the DB connection details
   * @param connectionDetials
   * @param connectionId
   */
  updateDBConnection(connectionDetials: any, connectionId: string): Observable<any> {
    return this._htttp.put(
      `${this.serverBaseUrl}/api/v2/dbconn/${connectionId}`,
      connectionDetials
    );
  }

  /**
   * Creates a new SFTP connection
   * @param connectionDetials
   */
  createSFTPConnection(connectionDetials: any): Observable<any> {
    return this._htttp.post(`${this.serverBaseUrl}/api/v2/sftp`, connectionDetials);
  }

  /**
   * Updates the SFTP connection details
   * @param connectionDetials
   * @param connectionId
   */
  updateSFTPConnection(connectionDetials: any, connectionId: string): Observable<any> {
    return this._htttp.put(`${this.serverBaseUrl}/api/v2/sftp/${connectionId}`, connectionDetials);
  }

  /**
   * Deletes the DB connection
   * @param connectionId
   */
  deleteDBConnection(connectionId: string): Observable<any> {
    return this._htttp.delete(`${this.serverBaseUrl}/api/v2/dbconn/${connectionId}`);
  }

  /**
   * Deletes the SFTP connection
   * @param connectionId
   */
  deleteSFTPConnection(connectionId: string): Observable<any> {
    return this._htttp.delete(`${this.serverBaseUrl}/api/v2/sftp/${connectionId}`);
  }

  /**
   * Updates the connection details as per the type
   */
  updateConnection(connId: string, type: string, reqObj: any): Observable<any> {
    return this._htttp.put(
      `${this.serverBaseUrl}/api/connections/${connId}/update/${type}`,
      reqObj
    );
  }

  /**
   * Deletes the connection details as per the type
   */
  deleteConnection(connId: string, type: string): Observable<any> {
    return this._htttp.delete(`${this.serverBaseUrl}/api/connections/${connId}/delete/${type}`);
  }
}
