import { Pipe, PipeTransform } from '@angular/core';
/**
 * Converts file size to KB,MG,GB
 * Accepts file size in bytes
 * Usage:
 *  value|fileSizeConverter
 * Example:
 *  {{ 2048 | fileSizeConverter }}
 */

@Pipe({ name: 'fileSizeConverter' })
export class FileSizeConverterPipe implements PipeTransform {
    transform(bytes: number, decimals: number) {
        if (bytes === 0) return '0 Bytes';

        const k = 1024;
        const dm = decimals < 0 ? 0 : decimals;
        const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];

        const i = Math.floor(Math.log(bytes) / Math.log(k));

        return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
    }
}