import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class SchedularService {

  serverBaseUrl = environment.serverBaseUrl;

  constructor(private _htttp: HttpClient) { }

  /**
   * Fetches the schedulars list from DB
   */
  getSchedularsList(): Observable<any> {
    return this._htttp.get(`${this.serverBaseUrl}/api/v2/scheduler`);
  }

  /**
   * Creates a new schedular
   * @param schedularDetails 
   */
  createNewSchedular(schedularDetails: any): Observable<any> {
    return this._htttp.post(`${this.serverBaseUrl}/api/v2/scheduler`, schedularDetails);
  }

  /**
   * Updates the schedular
   * @param schedularDetails 
   */
  updatedSchedular(schedularDetails: any, schedularId: string): Observable<any> {
    return this._htttp.put(`${this.serverBaseUrl}/api/v2/scheduler/${schedularId}`, schedularDetails);
  }
  /**
   * Starts the schedular
   * @param schedularId 
   */
  startSchedular(schedularId: string): Observable<any> {
    return this._htttp.post(`${this.serverBaseUrl}/api/v2/scheduler/${schedularId}/play`, { id: schedularId });
  }

  /**
   * Stops the schedular
   * @param schedularId 
   */
  stopSchedular(schedularId: string): Observable<any> {
    return this._htttp.post(`${this.serverBaseUrl}/api/v2/scheduler/${schedularId}/pause`, { id: schedularId });
  }

  /**
   * Deletes the schedular from DB
   * @param schedularId 
   */
  deleteSchedular(schedularId: string): Observable<any> {
    return this._htttp.delete(`${this.serverBaseUrl}/api/v2/scheduler/${schedularId}`);
  }
}
