import { Pipe, PipeTransform } from '@angular/core';
/**
 * Converts row col value to K, M, B.
 * Accepts row col value in original and formats it accordingly.
 * Usage:
 *  value|rowColValConverter
 * Example:
 *  {{ 150000 | rowColValConverter }}
 */

@Pipe({ name: 'rowColValConverter' })
export class RowColValConverterPipe implements PipeTransform {
    transform(val:number) {
        if(val == 0) {
            return 0;
        }
        else
        {        
          // hundreds
          if(val <= 999){
            return val ;
          }
          // thousands
          else if(val >= 1000 && val <= 999999){
            return (val / 1000) + 'K';
          }
          // millions
          else if(val >= 1000000 && val <= 999999999){
            return (val / 1000000) + 'M';
          }
          // billions
          else if(val >= 1000000000 && val <= 999999999999){
            return (val / 1000000000) + 'B';
          }
          else
            return val ;
          }
        }
}