import { Pipe, PipeTransform } from '@angular/core';
/**
 * Filters the normalization options as per the useGPU and project type
 */
@Pipe({
  name: 'normalizationOptions',
})
export class NormalizationOptionsPipe implements PipeTransform {
  transform(items: Array<any>, project: any): Array<any> {
    switch (project.type) {
      case 'predictive_modeling':
        if (!project.useGPU) {
          return items;
        } else {
          items = items.filter(
            (item) => item.value !== 'power_transform' && item.value !== 'log_transform'
          );
          return items;
        }
      case 'clustering':
        if (!project.useGPU) {
          return items;
        } else {
          items = items.filter(
            (item) =>
              item.value !== 'power_transform' &&
              item.value !== 'log_transform' &&
              item.value !== 'quantile_transform'
          );
          return items;
        }
      case 'timeseries':
        if (!project.useGPU) {
          return [
            { name: 'None', value: 'none' },
            { name: 'Min Max', value: 'minmax_scaler' },
            { name: 'Quantile Transform', value: 'quantile_transform' },
            { name: 'Power Transform', value: 'power_transform' },
          ];
        } else {
          return [
            { name: 'None', value: 'none' },
            { name: 'Min Max', value: 'minmax_scaler' },
          ];
        }
      default:
        return items;
    }
  }
}
