import { DatePipe, DecimalPipe } from '@angular/common';
import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'edaValueFormatter',
})
export class EdaValueFormatterPipe implements PipeTransform {
  constructor(private _decimalPipe: DecimalPipe, private _datePipe: DatePipe) {}

  transform(value: any): any {
    if (typeof value === 'number') {
      return this._decimalPipe.transform(value, '1.0-2');
    } else if (Date.parse(value)) {
      return this._datePipe.transform(value, 'dd/MM/yy');
    } else {
      return value;
    }
  }
}
