import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class DataDriftService {
  serverBaseUrl = environment.serverBaseUrl;
  formData: FormData = new FormData();

  constructor(private _http: HttpClient) { }

  /**
   * Fetches all the drift configurations from DB
   */
  getAllSettings(projectId: string): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/v2/projects/${projectId}/config`);
  }

  /**
   * Deletes the drift setting from DB
   * @param projectId 
   * @param configId 
   * @returns 
   */
  deleteSetting(projectId: string, configId: string): Observable<any> {
    return this._http.delete(`${this.serverBaseUrl}/api/v2/projects/${projectId}/delete/${configId}`);
  }

  /**
   * Creates a new drift setting record in DB
   * @param projectId 
   * @param settingDetails 
   * @returns 
   */
  createNewSetting(projectId: string, settingDetails: any): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/v2/projects/${projectId}/driftConfig`, settingDetails);
  }

  /**
   * Updates the selected drift settings details in DB
   * @param projectId 
   * @param configId 
   * @param settingDetails 
   * @returns 
   */
  updateSetting(projectId: string, configId: string, settingDetails: any): Observable<any> {
    return this._http.put(`${this.serverBaseUrl}/api/v2/projects/${projectId}/update/${configId}`, settingDetails);
  }

  /**
   * Fetches all the generated drift reports from DB
   * @param projectId 
   * @returns 
   */
  getAllReports(projectId: string): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/v2/projects/${projectId}/datadrift/list`);
  }

  /**
   * Deletes the report from DB
   * @param projectId 
   * @param reportId 
   * @returns 
   */
  deleteReport(projectId: string, reportId: string): Observable<any> {
    return this._http.delete(`${this.serverBaseUrl}/api/v2/projects/${projectId}/datadrift/${reportId}`);
  }

  /**
   * Fetches the report details from DB
   * @param projectId 
   * @param reportId 
   * @returns 
   */
  getReportDetails(projectId: string, reportId: string): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/v2/projects/${projectId}/datadrift/${reportId}`);
  }

  /**
   * Starts the drift analysis
   * @param trainingId 
   * @param modelId 
   * @param featurewiseDetails 
   * @returns observable 
   */
  startDriftAnalysis(trainingId: string, modelId: string, featurewiseDetails: any): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/v2/training/${trainingId}/models/${modelId}/datadrift/start`, featurewiseDetails);
  }
}
