import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root',
})
export class SampleDatasetService {
  serverBaseUrl = environment.serverBaseUrl;

  constructor(private _htttp: HttpClient) {}

  /**
   * Creates a new sample Dataset
   * @param reqObj
   * @returns
   */
  createDataset(file: any, formValue: any): Observable<any> {
    const formData: FormData = new FormData();
    formData.append('file', file);
    formData.append('data', JSON.stringify(formValue));
    return this._htttp.post(`${this.serverBaseUrl}/api/sampledatasets`, formData);
  }

  /**
   * Fetches the Datasets list
   */
  getAllDatasets(): Observable<any> {
    return this._htttp.get(`${this.serverBaseUrl}/api/sampledatasets`);
  }

  /**
   * Deletes the Dataset from DB
   * @param DatasetId
   */
  deleteDataset(DatasetId: string): Observable<any> {
    return this._htttp.delete(`${this.serverBaseUrl}/api/sampledatasets/${DatasetId}`);
  }

  /**
   * Updates the Dataset details
   * @param userId
   */
  updateDataset(DatasetData: any, DatasetId: string): Observable<any> {
    return this._htttp.put(`${this.serverBaseUrl}/api/sampledatasets/${DatasetId}`, DatasetData);
  }
}
