import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ReportsService {
  serverBaseUrl = environment.serverBaseUrl;

  constructor(private _http: HttpClient) { }

  /**
   * Fetches the reports list from DB 
   */
  getReportsList(): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/v2/projects/shareOutput/byUser`);
  }

  /**
   * Fetches the base64 encode for type pdf, png, jpg and tiff
   * @param reportData selected report data
   * @param projectId selected report project id
   */
  getImageEncode(reportData: object, projectId: string): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/v2/projects/${projectId}/output/data/reportpreview`, reportData);
  }

  /**
   * Fetches the advance EDA report html
   * @param projectId 
   * @param edaId 
   */
  getAdvanceEdaReport(projectId: string, edaId: string): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/v2/projects/${projectId}/eda/${edaId}/advedainfo`, { responseType: 'text' });
  }

  /**
   * Fetches the file data to preiview
   * @param reportData 
   * @param projectId 
   * @param outputId 
   */
  getFilePreview(reportData: object, projectId: string, outputId: string): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/v2/projects/${projectId}/output/${outputId}/preview`, reportData);
  }

  /**
   * Fetches the advance file data report html
   * @param projectId 
   * @param edaId 
   */
  getFileDataReport(projectId: string, dataId: string): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/v2/projects/${projectId}/data/${dataId}/report`, { responseType: 'text' });
  }
}
